import http from '../http-common.js';

class DataServices{
    create(data){
        return http.post("/allthings", data)
    }
    getAll(){
        return http.get("/allthings")
    }
    getById(id){
        return http.get(`/allthings/${id}`)
    }
    update(id, data){
        return http.put(`/allthings/${id}`,data)
    }
    delete(id){
        return http.delete(`/allthings/${id}`)
    }
    findByTitle(title){
        return http.get(`/allthings?title=${title}`)
    }

}

export default new DataServices();