import { createRouter, createWebHistory } from "vue-router";
import HomeView from "../views/Mains.vue";

const routes = [
  {
    path: "/",
    alias:"/home",
    name: "mains",
    component: HomeView,
  },
  {
    path: "/allthings/:id",
    alias:"/details",
    name: "details",    
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Details.vue"),
  },
  {
    path: "/add",
    alias:"/add-item",
    name: "add",    
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Add.vue"),
  },
  {
    path: "/delete",
    alias:"/delete-item",
    name: "delete",    
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Delete.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
