<template>
    <div v-if="viewing" id="eachItem" @mouseenter="enterMouse" @mouseleave="leaveMouse"
        :style="computedStyle"
    >
        {{ sT.contents }}
    </div>
</template>
<script>
export default{
    props : ['sT'],
    data(){
        return{
            viewing : false,
            options : {
                left : true,
                top : true,
                valueX : 0,
                valueY : 0
            }            
        }
    },
    mounted() {
        this.determineData();
    },
    computed :{
        computedStyle(){
            if(Object.keys(this.sT).length == 0){
                return {
                    display : none
                }
            }
            console.log(this.options)
            if(this.options.left && this.options.top){
                return {
                    left : this.options.valueX,
                    top : this.options.valueY
                }
            }else if(this.options.left && !this.options.top){
                return {
                    left : this.options.valueX,
                    bottom : this.options.valueY
                }
            }else if(!this.options.left && this.options.top){
                return {
                    right : this.options.valueX,
                    top : this.options.valueY
                }
            }else{
                return {
                    right : this.options.valueX,
                    bottom : this.options.valueY
                }
            }
            
        }
    },
    methods: {
        determineData(){
        if(Object.keys(this.sT).length != 0){ 
            this.viewing = true;
            this.$nextTick(() => {
                this.setNewWeb();
            });        
        }        

        },
        setNewWeb(){
            let currentX = window.innerWidth;
            let currentY = window.innerHeight;
            currentX * 0.7 <= this.sT.ofX ?this.options.left = false : this.options.left = true; 
            currentY * 0.85 >= this.sT.ofY ?this.options.top = true : this.options.top = false; 
            this.options.left ?  this.options.valueX = this.sT.ofX + "px" : this.options.valueX = currentX - (this.sT.ofX) + "px";
            this.options.top ?  this.options.valueY = this.sT.ofY + "px" : this.options.valueY = currentY - (this.sT.ofY) + "px";
            
            //this.setStyle()
        },
        setStyle(){
            // const EachItem = document.querySelector('#eachItem');
            // let setX = this.options.valueX + "px";
            // let setY = this.options.valueY+ "px";          
            // if(this.options.left && this.options.top){
            //     EachItem.style.left = setX;
            //     EachItem.style.top = setY;
            // }else if(this.options.left && !this.options.top){
            //     EachItem.style.left = setX;
            //     EachItem.style.bottom = setY;
            // }else if(!this.options.left && this.options.top){
            //     EachItem.style.right = setX;
            //     EachItem.style.top = setY;
            // }else{
            //     EachItem.style.right = setX;
            //     EachItem.style.bottom = setY;
            // }
            const EachItem = document.querySelector('#eachItem');
            let setX = this.options.valueX *0.9 + "px";
            let setY = this.options.valueY *0.9 + "px";          
            if(this.options.left && this.options.top){
                EachItem.style.left = setX;
                EachItem.style.top = setY;
            }else if(this.options.left && !this.options.top){
                EachItem.style.left = setX;
                EachItem.style.bottom = setY;
            }else if(!this.options.left && this.options.top){
                EachItem.style.right = setX;
                EachItem.style.top = setY;
            }else{
                EachItem.style.right = setX;
                EachItem.style.bottom = setY;
            }
        },
        enterMouse(e){
            e.stopPropagation();
            console.log("teeett")
            this.$emit('mouseAction',true)
        },
        leaveMouse(e){
            e.stopPropagation();
            this.$emit('mouseAction',false)
        }
    },

}
</script>
<style>
    #eachItem{
        position: absolute;
        width: 30%;
        height: 15%;
        box-shadow: 1px 1px 5px black;
    }
</style>