<template>
    <div v-if="lists.length > 0">
        <table id="allthings">
            <tr>
                <th>Id</th>
                <th>Title</th>
                <th>Likes</th>
                <th>Comments</th>
            </tr>
            <tr v-for="(item, indexs) in lists"
                :key="indexs"
                class="listz"
                :class="{acitved :indexs == cI}"
                @mouseenter="showEachList(item, indexs, $event)"
                @mouseleave="goneEachList"
                @click="goToDetails(item)"
            >
                <td>{{item.id}}</td>
                <td>{{item.title}}</td>
                <td>{{item.likes}}</td>
                <td>{{item.comments}}</td>
                <Description v-if="cI>=0" :sT="sT" @mouseAction="callMouse"></Description>
            </tr>
        </table>
    </div>
    <div v-else>
        <h1 class="h1" @click="retriveLists">There is no datas.... :&lt;</h1>
    </div>
    
</template>
<script>
import ds from '@/services/DataServices.js'
import Description from '@/components/Description.vue'

export default{
    name : "mains",
    components : {
        Description
    },
    data(){
        return{
            lists : [],
            cT : null,
            cI : -1,
            title : "",
            sT : null,
            actions : -1
        }
    },
    methods : {
        retriveLists(){
            ds.getAll()
                .then(res =>{
                    this.lists = res.data;
                })
                .catch(e=>{
                    console.error(e.response.data)
                })
        },
        showEachList(item, index, e){       
             
            this.cT = item;
            this.cI = index;
            let newObj = { ofX : e.clientX, ofY : e.clientY};
            this.sT = Object.assign(item, newObj);            
        },
        goneEachList(){
            if(this.actions<0){
                this.cT = null;
                this.cI = -1;
            }else if(this.actions==0){
                this.actions = -1;
                this.cT = null;
                this.cI = -1;
            }            
                
                        
        },
        callMouse(data){
            
            if(data){
                this.actions=1;
                console.log("in")
            }else{
                this.actions=0;
                console.log("out")
            }
        },
        goToDetails(item){
            this.$router.push({name : 'details', params : {id: item.id}})
        }
        
    },
    mounted(){
        this.retriveLists()
    }
}
</script>

<style>
.container{
    position: relative;
    
}
#allthings{
    margin-left: 15%;
    width : 70%
}
.listz{
    background-color: antiquewhite;
}
</style>