<template>
  <nav>
    <div>
      <router-link to="/">Home</router-link>
    </div>
    <div>
      <router-link to="/add">Add</router-link>
    </div>
    <div v-if="currentView">
      <router-link to="/delete">Delete</router-link>
    </div>


  </nav>
  <div id="container">
