  </div>
</template>

<script>
export default {
  data() {
    return {
      currentView: false
    }
  },
  mounted() {
    this.$router.beforeEach((to, from, next) => {
//       beforeEach Guard:

// The beforeEach guard is called before each navigation to a new route.
// It allows you to perform actions such as authentication checks, route redirection, or setting up some data before the navigation occurs.
// You can use next() to control whether the navigation should proceed. For example, calling next() allows the navigation to continue, while calling next(false) cancels the navigation, and calling next('/some-route') redirects to another route.
// afterEach Guard:

// The afterEach guard is called after each navigation to a new route.
// It allows you to perform actions after the navigation has occurred, such as logging, analytics tracking, or DOM manipulation.
// Unlike beforeEach, the afterEach guard does not have the ability to control navigation using next(). It is purely for post-navigation tasks.
// In summary, beforeEach is used for tasks that need to be performed before navigation, and it has the ability to control whether navigation should proceed. On the other hand, afterEach is used for tasks that need to be performed after navigation has already taken place, and it does not affect the navigation flow.

      // to: 이동할 경로에 대한 라우트 객체
      // from: 현재 경로에서 이동할 경로로의 라우트 객체
      // next: 이 함수를 호출하여 네비게이션을 계속할 수 있음을 나타냄
      // 예시: 특정 조건에 따라 네비게이션을 허용 또는 거부하는 로직
      //if (to.path === '/admin' && !this.$store.state.user.isAdmin) {
      // 관리자 페이지로의 접근은 관리자 권한이 있는 사용자에게만 허용
      //  next('/login'); // 로그인 페이지로 리다이렉트
      //} else {
      // 특별한 조건이 없으면 네비게이션을 계속 진행
      //  next();
      //}
      //최초로 사이트를 오픈했을 때:
      // to: /
      // from: null 또는 이전 페이지가 없으므로 없는 상태

      // About 페이지로 이동했을 때:
      // to: /about
      // from: /

      // About 페이지에서 Home 페이지로 이동했을 때:
      // to: /
      // from: /about
      if(to.path.startsWith('/allthings') && to.params != null) this.currentView =true;
      else this.currentView =false;

      next()
    })
  }

}


</script>
